
import { defineComponent } from 'vue'
export default defineComponent({
  props: {
    listData: {
      type: Array,
      require: true
    },
    totalCount: {
      type: Number,
      default: 0
    },
    propList: {
      type: Array,
      require: true
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    page: {
      type: Object,
      default: () => ({
        currentPage: 0,
        pageSize: 10
      })
    },
    // 把childrenProps绑定到table里面，通过v-bind
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emit: ['emitchange', 'update:page'],
  setup(props, { emit }) {
    // 下面是监听页码条数等的改变
    const handleSelectChange = (value: string) => {
      // console.log(value)
      //会把选中的数据以数组的形式传过来
      emit('emitchange', value)
    }
    const handleSizeChange = (pageSize: number) => {
      // 发送出去，通过emit
      // 拿到原来的值，再把pageSize的值放进去
      // console.log({ ...props.page }, 1111111)
      emit('update:page', { ...props.page, pageSize })
    }
    const handleCurrentChange = (currentPage: number) => {
      // console.log({ ...props.page }, 2222)
      emit('update:page', { ...props.page, currentPage })
    }
    return {
      handleSelectChange,
      handleSizeChange,
      handleCurrentChange
    }
  }
})
